@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cormorant:wght@400;500&display=swap');
.modal-window{

  max-width: 100%;
  max-height:100%;

  width: inherit;
  overflow:auto;
  z-index: 999;
  
  display: flex;
  justify-content: flex-end;

  background: white;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  height: inherit;


  .modal-window__form {

    position: absolute;

    width: calc(100% - 60px);
    height: calc(100% - 60px);

    margin:30px;

    border: 1px solid rgba(166, 142,82, 0.5);
    padding: 50px;

    .modal-window__title {
      margin:0; 
      
      width: 250px;
      margin-bottom: 30px; 

      font-family: 'Cormorant', serif;
      font-size:68px;
      font-weight: 500;
      
    
    }
    .call-form {
      display: flex;
      flex-direction: column;
      
      color: #757575;
      .call-form__input {
        width: 265px;
        
        border:none;
        border-bottom: 1px solid #DDDDDD;

        margin-bottom: 20px;

        font-size: 14px;
        color: #757575;
        line-height: 24px;
      }
      .call-form__input.phone-margin-bottom{
        margin-bottom: 40px;
      }
      .call-form__submit {
        width: 182px;
        height: 38px;
        border-radius: 3px;

        border: none;

        background: #A68E52;
        color:white;
        text-transform: uppercase;
        font-style: normal;
        font-weight: 400;
        font-size: 10px;
        line-height: 26px;
      }
    }
  }
  .modal-window__image { 
    width: 50%;
    background-image: url('/../../assets/images/modal_house.png');
    background-repeat: no-repeat;
    background-position: right;

    .close-button {
      position:absolute;
      right:40px;
      top:40px;
    
      width:50px;
      height:50px;
      background: rgba(0,0,0, 0.1);
      border: 0.5px solid #A68E52;
      border-radius:50%;
      display:flex;
      align-items: center;
      justify-content: center;
      .close-button__inner {
        background-color: #fff;
        width:30px;
        height:30px;
    
        display: flex;
        justify-content: center;
        align-items: center;
    
        border: 0.5px solid #A68E52;
        background: white;
        border-radius: 50%;
    
        button.mfp-close{
          width: 40px;
          height: 40px;
          padding:0;
          position: static;
        
          background:  url('/../../assets/images/close.png');
          background-size: 10px 10px;
          background-position:center;
          background-repeat: no-repeat;
        
        }
 
      }
    }
  }
}



.white-popup {
  position: relative;
  background: #FFF;
  padding: 25px;
  width: 770px;

  margin: 0 auto; 
  height: 500px;
  
}

.mfp-zoom-in {
  .mfp-with-anim {
    opacity: 0;
    transition: all 0.2s ease-in-out; 
    transform: scale(0.8); 
  }
  
  &.mfp-bg {
    opacity: 0;
	  transition: all 0.3s ease-out;
  }
  
  &.mfp-ready {

    .mfp-with-anim {
      opacity: 1;
      transform: scale(1); 
    }
    &.mfp-bg {
      opacity: 0.8;
    }
  }
  
  &.mfp-removing {
    
    .mfp-with-anim {
      transform: scale(0.8); 
      opacity: 0;
    }
    &.mfp-bg {
      opacity: 0;
    }
    
  }
  
}


