@import '../styles/templates-styles/_slider.scss';
@import '../styles/templates-styles/variables/hotspots.scss';
@import url('https://fonts.googleapis.com/css2?family=Cormorant:wght@400;500;600&family=Source+Sans+Pro:wght@300;400&display=swap');
*{
  font-family: 'Cormorant', serif;
  margin:0;
}


.container { 
  max-width: 1920px;
  margin: 0 auto;
}

.introduction-area { 
  height: 100vh;
  overflow: hidden;

  .header {

    position: absolute;
    z-index: 999;

    display: flex;
    justify-content: flex-start;

    margin-left: 45px;
    margin-top:20px;

    color:white;
    .header__logo {
      margin-right:180px;
      .header__img{ 
        width: 60px;
        height: 70px;
      }
    }
    .header__call-section{
      display: inherit;
      width:230px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      .burger-menu {
        display: none;
        img {
          width:30px;
          height:30px;
        }
      }
      .call-btn{
        margin-right: 24px;
      }
      .call-info{
        width:200px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        transition: display 3s;

        .call-number{
          color: #A68E52;
          font-size: 16px;
          margin-bottom: 8px;
          font-family: 'Source Sans Pro', sans-serif;
          display: block;
        }
        .call-text{
          padding-bottom: 4px;
          border-bottom: 1px solid #A68E52;
          font-family: 'Source Sans Pro', sans-serif;
        }
      }
      .call-btn, .current-language{
        width: 30px;
        height:30px;

        border: 1px solid #757575;
        border-radius: 50%;

        background-color: transparent;
      }
      .call-btn {
        display: inline;
      }
      .current-language{
        font-size: 10px;
        color: white;
  
      }

    }

  }
}

section.contacts {
  width:100%;

  display: flex;
  justify-content: flex-start;

  position: absolute;
  z-index: 999;

  bottom:3%;

  .contacts__address {
    width: 100%;
    display: flex;

    color: #DDDDDD;
    opacity: 0.7;

    text-transform: uppercase;

    padding-left: 100px;
    .sub_contacts {
      margin-right: 20px;
    }
  }
  .sub_logo{
    width:100%;

    display: flex;
    justify-content: flex-end;

    padding-right: 40px;
  }
}

@include for-desktop-up{
  .introduction-area{
    .header {
      // width: 600px;

    }
  }
}
@include for-tablet-landscape-up {
  .introduction-area{
    .header{
      .header__logo {
        // margin-right: 120px;
      }
      // width: 500px;
    }
  }
}


@include for-phone-only {
  .introduction-area {
    .header{
      justify-content: space-between;
      width: 80%;

      .header__logo {
        margin-right: 0px;
      }
      .header__call-section {
        width: 90px;
        .call-btn {
          height: 40px;
          width: 40px;
          display: flex;
          align-items: center;
          margin-right: 20px;
          img {
            margin: 0 auto;
            height:15px;
            width:15px;
          }
        }
        .call-info-show {
          opacity:0;
          display: none;
          display: flex;
          position: absolute;
          width: 160px;
          top: 60px;
      
          right: 0px;
          transition: opacity 1s ease-in-out;
        }
        .toggle {
          opacity: 1;


        }
        .burger-menu{
          
            display:block;
        }
       
      }

  }
}
}


@media (max-width:924px) {
  .contacts {
    .contacts__address {
      .sub_contacts {
        font-size: 0.8em;
      }
    }
  }
}

@media (max-width:798px) {
  section.contacts {
    .contacts__address {
      padding-left: 80px;

      .sub_contacts {
        font-size: 0.7em;
      }
    }
  }
}
@media (max-width:725px) {
  section.contacts {
    .contacts__address {
      padding-left: 70px;

      .sub_contacts {
        font-size: 0.7em;
      }
    }
    .sub_logo {
      width: auto;
    }
  }
}
@media (max-width:695px) {
  section.contacts {
    .contacts__address {
      padding-left: 50px;

      .sub_contacts {
        font-size: 0.7em;
      }
    }
  }
}

@media (max-width:445px) {
  section.contacts {
    .contacts__address {
      padding-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      .sub_contacts {
        font-size: 0.7em;
        margin:0;
        margin-right:10px;
        img {
          display: none;
        }
      }
    }
  }
}
@media (max-width:445px) {
  section.contacts {
    .contacts__address {
      padding-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      .sub_contacts {
        font-size: 0.6em;
        margin:0;
        margin-right:10px;
        img {
          display: none;
        }
      }
    }
    .sub_logo {
      img {
        height:20px;
        width: 20px;
      }
    }
  }
}
@media all and (orientation: landscape), all and (max: 480px) {
  .introduction-area .header {
    margin-top:10px;
    .header__logo .header__img {
      width: 40px;
      height: 70px;
  }
  }
}