@import '../styles/templates-styles/variables/hotspots.scss';

.slick-slider{
  margin:0 auto;
}

.slider {
  margin: 0 auto;
  position: relative;
  height: 100%;

  .slider-item {
    height: 100vh;
    .slider__content {
      position: relative;
      bottom: 70%;
      margin-left: 110px;
      .slider__title {
        font-size: 2em;
        color: #A68E52;
        font-weight: normal;
      }
      
      .slider__text{
        font-size: 4em;
        font-weight: 500;
        color: white;
        max-width:500px;
      }
        
      .slider__complex-info-btn{
        width: 180px;

        margin-bottom:20px;
        margin-top:20px;
        
      
        padding:16px;
      
        background: #A68E52;
        border:none;
        border-radius: 3px;
        
        
        text-align: center;
      
        color:white;
        font-size: 12px;
        font-weight: 100;
        text-transform: uppercase;
      
      }
    }
    .slider__image {
      width: 100%;
      height: 100%;

      object-fit: cover;
    }
  }

  .slick-track{
    display: flex;
  }

  .slick-arrow{
    margin-top:10px;
    margin-right: 30px;
    color:transparent;
    background-color: #DDDDDD;

    border:none;

    height:1px;
    width: 75px;
    &:hover{
      cursor: pointer;
    }
  }


  .next:after {
    position: relative;
    content: "";
    top: -10px;
    right: -25px;
    width: 0;
    height: 0;
    display: inline-block;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-left: 10px solid #DDDDDD;
    border-right: 10px solid transparent;
  }

  .prev:before{
    position: relative;
      content: "";
      top: -9px;
      left: -45px;
      width: 0;
      height: 0;
      display: inline-block;
      border-top: 10px solid transparent;
      border-bottom: 10px solid transparent;
      border-right: 10px solid #DDDDDD;
      border-left: 10px solid transparent;
  }

  .slick-dots{

    margin-top:10px;

    display: flex;
    list-style: none;
    padding: 0;
    li {
      margin-right: 25px;
    button{
      padding:0;
      width:16px;
      height:16px;
      border-radius: 50%;
      border:none;
      background: #757575;
      color:transparent
    
    }

    }
    li.slick-active{
      button{
        background: pink;
        opacity: 0.2;
      }
    }
  }

}

//orientation landscape

@media (max-width:499px) { //499px
  .slider {
    .slider-item {
      .slider__title, .slider__text, .slider__complex-info-btn{
        margin-left:60px;
      }
      .slider__text {
        font-size: 3em;
      }
      .slider__title {
        bottom: 60%;
        font-weight: bold;
        font-size: 1.6em;
      }
    }

    .slick-dots {
      left:60px;
    }
    .slick-arrow.slick-prev{
      left:180px;
    }
  
    .slick-arrow.slick-next{
      left:290px;
    }
  }
}
